/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
hr.wp-block-mypreview-spacer {
	margin-top: 0;
	margin-bottom: 0;
    border: none;
    max-width: 100%;
}

/*--------------------------------------------------------------
# Horizontal alignment
--------------------------------------------------------------*/
/*rtl:begin:ignore*/
.hrz-align-dk-center {
	margin-right: auto;
	margin-left: auto;
}

.hrz-align-dk-right {
	margin-right: 0;
	margin-left: auto;
}

.hrz-align-dk-left {
	margin-left: 0;
	margin-right: auto;
}

@media (max-width: 80em) {
	.hrz-align-lp-center {
		margin-right: auto;
		margin-left: auto;
	}

	.hrz-align-lp-right {
		margin-right: 0;
		margin-left: auto;
	}

	.hrz-align-lp-left {
		margin-left: 0;
		margin-right: auto;
	}
}

@media (max-width: 60em) {
	.hrz-align-tb-center {
		margin-right: auto;
		margin-left: auto;
	}

	.hrz-align-tb-right {
		margin-right: 0;
		margin-left: auto;
	}

	.hrz-align-tb-left {
		margin-left: 0;
		margin-right: auto;
	}
}

@media (max-width: 37.5em) {
	.hrz-align-sp-center {
		margin-right: auto;
		margin-left: auto;
	}

	.hrz-align-sp-right {
		margin-right: 0;
		margin-left: auto;
	}

	.hrz-align-sp-left {
		margin-left: 0;
		margin-right: auto;
	}
}
/*rtl:end:ignore*/

/*--------------------------------------------------------------
# Device visibility
--------------------------------------------------------------*/
@media (min-width: 80.0625em) {
	.hide-dk {
		display: none;
	}
}

@media (max-width: 80em) and (min-width: 60.0625em) {
	.hide-lp {
		display: none;
	}
}

@media (max-width: 60em) and (min-width: 37.5625em) {
	.hide-tb {
		display: none;
	}
}

@media (max-width: 37.5em) {
	.hide-sp {
		display: none;
	}
}